import { NOT_FOUND, GET_PROFILES, GET_PROFILE, PROFILE_LOADING, CLEAR_CURRENT_PROFILE } from '../actions/types';
const initialState = {
    profile: null,
    profiles: null,
    loading: false
}//end innitial state
export default function(state = initialState, action){
   switch(action.type){
       case PROFILE_LOADING:
            return {
                ...state,
                loading: true
            }
        case GET_PROFILE:
            return{
                ...state,
                profile: action.payload,
                loading: false 
            }
        case GET_PROFILES:
            return{
                ...state,
                profiles: action.payload,
                loading: false 
            }
        case CLEAR_CURRENT_PROFILE:
            return {
                ...state,
                profile: null
            }
            case NOT_FOUND:
            return {
                ...state,
                profile: null
            }
        default:
          return state;
    }
}//end export default